@import url('https://unpkg.com/primer/build/build.css');

html {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    color: #24292e;
}

body {
    max-width: 1000px;
    padding: 15px;
    margin-left: auto;
    margin-right: auto;
    background-color: red;
}

#header {
    text-decoration: none;
    color: #24292e;
    font-size: 1.5em;
}

h2 {
    padding-bottom: 0.3em;
    font-size: 1.5em;
    border-bottom: 1px solid #eaecef;
}

img {
    object-fit: cover;
    width: 230px;
    height: 230px;
    border: 1px solid rgb(240, 240, 240);
    transform: rotate(-4deg);
    padding: 10px 10px 40px 10px;
    -webkit-box-shadow: 5px 5px 33px -18px rgba(112, 112, 112, 0.79);
    -moz-box-shadow: 5px 5px 33px -18px rgba(112, 112, 112, 0.79);
    box-shadow: 5px 5px 33px -18px rgba(112, 112, 112, 0.79);
}

.post {
    width: 275px;
}

.post-title {
    text-decoration: none;
    color: #24292e;
    margin: 20px 0px 20px 10px;
    display: inline-block;
    font-size: 1.2rem;
}

.post-tag {
    background-color: #eaecef;
    border-radius: 5px;
    padding: 5px;
    font-size: .8rem;
    margin: 0 0 0 8px;
    display: inline-block;
}

.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 50px;
}